    transform:translateY(10px)
  }

  100%{
    -webkit-transform:translateY(0px);
    transform:translateY(0px)
  }
}

@-webkit-keyframes click-animation{
  0%{
    -webkit-transform:scale(1);
    transform:scale(1)
  }

  50%{
    -webkit-transform:scale(0.9);
    transform:scale(0.9)
  }

  100%{
    -webkit-transform:scale(1);
    transform:scale(1)
  }
}

